.App {
  /* text-align: center; */
  display:flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin:1rem;
  width:100%;
  height:100%;

}
/* #BackImg{
  background-image: url('/Assets/Green-Tropical-Plant-Wallpaper-Mural-Plain.jpg');
} */
#About.App{
  width:70%;
  height:100%;
  max-width: 50%;
  max-height: 100%;

  background-color: peru;
}
#About.App > p{
  padding: .6rem;
  margin: .4rem;
  max-width:55%;
  height: 100%;
  background-color: peachpuff;

}
#About.App > ul{
  padding: 1.3rem;
  margin: .4rem;

  max-width:55%;
  height: 100%;
  background-color: peachpuff;
  
}
.App > .pale{
  
  background-color: palegreen;
}
.App.Form{
  /* display: flex;
  align-items: center;
  justify-content: center; */
  box-shadow: 0 -1px 0 #ec1313, 0 0 2px rgba(212, 209, 30, 0.603),
  0 2px 4px rgba(0, 0, 0, 0.24);
  background-color: silver;
  height:100%;
  max-width: 40%;
}
.App > h1{
  padding: .6rem;
  margin: .4rem;
  box-shadow: 0 -1px 0 #e0e0e0, 0 0 2px rgba(0, 0, 0, 0.12),
  0 2px 4px rgba(0, 0, 0, 0.24);
  
  background-color: silver;
}
.App > p{
  padding: .2rem;
  margin: .4rem;
  max-width:35%;
  height: 100%;
  background-color:silver ;
}

.App > form.App{
  max-width:20rem;
  box-shadow: .1rem .2rem .1rem grey;
  height:100%;
  background-color: lightgrey;
}
.App > label {
  font-family: 'Ubuntu';
  border-radius:30%;
  border-bottom-left-radius: 2% black;
  padding:.1rem;
  margin:.2rem;
}
.App > input{
  box-shadow: .3rem .2rem .1rem grey;
  max-width: 80%;
  min-width:25%;
  padding:.1rem;
  margin:.2rem;
}
.App-logo {
  /* height: 40vmin; */
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 2s linear;
  }


}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    translate: 30px;
  }
  to {
    translate: -30px;
  }

  

  
}
